<template>
  <div id="app">
    <div id="nav">

      <div class="left-section">
        <router-link :to="{ name: 'main' }">Vue Recipes</router-link>|
        <router-link :to="{ name: 'search' }">Search</router-link>|
      </div>

      <div class="right-section">

        <span v-if="!$root.store.username">
          Hello Guest:
          <router-link :to="{ name: 'register' }">Register</router-link>|
          <router-link :to="{ name: 'login' }">Login</router-link>|
        </span>
        <span v-else>
          Hello {{ $root.store.username }}: <button @click="Logout">Logout</button>|
          <div class="dropdown">
            <button class="dropbtn">Personal ▼</button>
            <div class="dropdown-content">
              <router-link :to="{ name: 'favorites' }">Favorites</router-link>
              <router-link :to="{ name: 'myRecipes' }">My Recipes</router-link>
              <router-link :to="{ name: 'myFamilyRecipes' }">My Family Recipes</router-link>
            </div>
          </div>
          <router-link :to="{ name: 'createRecipe' }">Create Recipe</router-link>|

        </span>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",
  methods: {
    Logout() {
      this.$root.store.logout();
      this.$root.toast("Logout", "User logged out successfully", "success");

      this.$router.push("/").catch(() => {
        this.$forceUpdate();
      });
    }
  }
};
</script>



<style lang="scss">
@import "@/scss/form-style.scss";

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  min-height: 100vh;
}

#nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}

.left-section {
  display: flex;
  align-items: center;
}

.right-section {
  display: flex;
  align-items: center;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropbtn {
  background-color: #fff;
  color: #000;
  padding: 10px;
  border: none;
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  color: #000;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}
</style>
